import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { TextField } from '@material-ui/core';

export default function FormDialog({open,handleClose,data,onChange,handleFormSubmit}) {
 const {MaP, MaPL, TenPL, DaXoa, NgayTao, NguoiTao, NgaySua, NguoiSua}=data

  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{MaP?"Sa thng tin":"Thm sch mi"}</DialogTitle>
        <DialogContent>
         <form>
            <TextField id="MaPL" value={MaPL} onChange={e=>onChange(e)} placeholder="Nhp m phn loi" label="M Phn Loi" variant="outlined" margin="dense" fullWidth />
             <TextField id="TenPL" value={TenPL} onChange={e=>onChange(e)} placeholder="Nhp tn phn loi" label="Tn Phn Loi" variant="outlined" margin="dense" fullWidth />
             <TextField id="DaXoa" value={DaXoa} onChange={e=>onChange(e)} placeholder=" xa" label="Trng thi" variant="outlined" margin="dense" fullWidth />
             <TextField id={MaP?"NgaySua":"NgayTao"} value={MaP?`${NgaySua}`:`${NgayTao}`} onChange={e=>onChange(e)} placeholder="" label={MaP?"Ngy Sa":"Ngy To"} variant="outlined" margin="dense" fullWidth />
             <TextField id={MaP?"NguoiSua":"NguoiTao"} value={MaP?`${NguoiSua}`:`${NguoiTao}`} onChange={e=>onChange(e)} placeholder="" label={MaP?"Ngi Sa":"Ngi To"} variant="outlined" margin="dense" fullWidth />
         </form>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="secondary" variant="outlined">
            Cancel
          </Button>
          <Button  color="primary" onClick={()=>handleFormSubmit()} variant="contained">
            {MaP?"Sa":"Xc Nhn"}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
